*, :before, :after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #fff;
  grid-template-rows: auto 1fr auto;
  justify-content: center;
  align-items: start;
  height: 100vh;
  font-family: Poppins, sans-serif;
  font-size: 18px;
  font-weight: 500;
  display: grid;
}

#library {
  background-color: #fff;
  grid-template-columns: repeat(auto-fill, minmax(290px, 1fr));
  gap: 50px;
  width: 70vw;
  padding: 30px 0;
  display: grid;
}

.card {
  background-color: #fff7e8;
  border: 2px solid #59a1a5;
  border-left-width: 10px;
  border-right: 1px solid #ffd88f;
  border-radius: 12px;
  flex-direction: column;
  flex: 1;
  gap: 8px;
  padding: 15px;
  display: flex;
}

.status-container {
  justify-content: center;
  display: flex;
}

.change-status-button {
  background-color: #59a1a5b5;
  margin: 0 auto;
}

.remove-button {
  background-color: #ff7893c5;
  margin: .3rem auto 0;
}

.change-status-button, .remove-button {
  border: none;
  border-radius: 7px;
  width: 40%;
  min-height: 1.4rem;
  font-size: .9rem;
}

.empty-state {
  text-align: center;
  color: #555;
  align-items: center;
  gap: 20px;
  font-style: italic;
}

header {
  border-bottom: 1px solid #ff7893;
  justify-content: flex-start;
  align-items: center;
  width: 70vw;
  padding: 30px 0;
  display: flex;
}

.title {
  flex: 1;
  padding-left: 30px;
  font-size: 1.2em;
}

header img {
  height: 3.5em;
}

header button {
  background-color: #fff;
  border: none;
  flex-direction: column;
  justify-content: center;
  font-size: 28px;
  display: flex;
}

header button img {
  align-self: flex-end;
  height: 1em;
}

.modal {
  z-index: 1;
  text-align: center;
  background-color: #f5f5f5;
  border: 1px solid #59a1a5;
  border-radius: 6px;
  width: 600px;
  padding: 30px;
  font-size: 18px;
  transition: all .2s ease-in-out;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(0);
}

.modal img {
  align-self: center;
  height: 40px;
}

.modal.active {
  transform: translate(-50%, -50%)scale(1);
}

.add-book-form {
  text-align: left;
  flex-direction: column;
  gap: 30px;
  display: flex;
}

.book-form-unit {
  flex-direction: column;
  gap: 5px;
  display: flex;
}

.radio-container {
  justify-content: space-around;
  display: flex;
}

.radio-div {
  flex-direction: column;
  gap: 5px;
  display: flex;
}

.overlay {
  backdrop-filter: blur(5px);
  width: 100%;
  height: 100%;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
}

.overlay.active {
  display: block;
}

input {
  border: none;
  border-radius: 6px;
  outline: none;
  padding: 6px;
  font-size: 20px;
}

button[type="submit"], .close-modal-button {
  background-color: #59a1a5;
  border: none;
  border-radius: 7px;
  width: 60%;
  height: 40px;
  min-height: 1.4rem;
  margin: 0 auto;
  font-size: 22px;
}

.close-modal-button {
  background-color: #ff7893;
}

footer {
  border-top: 1px solid #ff7893;
  justify-content: center;
  padding: 30px;
  font-size: 20px;
  display: flex;
}
