/* reset */
*,
*::before,
*::after {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body {
  display: grid;
  grid-template-rows: auto 1fr auto;
  justify-content: center;
  align-items: start;
  font-family: "Poppins", sans-serif;
  font-size: 18px;
  font-weight: 500;
  background-color: white;
  height: 100vh;
}

#library {
  padding: 30px 0;
  background-color: white;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(290px, 1fr));
  width: 70vw;
  gap: 50px;
}

.card {
  display: flex;
  flex-direction: column;
  flex: 1;
  background-color: #fff7e8;
  padding: 15px;
  border: 1px solid #ffd88f;
  border-radius: 12px;
  border-left: 10px solid #59a1a5;
  border-top: 2px solid #59a1a5;
  border-bottom: 2px solid #59a1a5;
  gap: 8px;
}

.status-container {
  display: flex;
  justify-content: center;
}

.change-status-button {
  background-color: #59a1a5b5;
  margin: 0 auto;
}

.remove-button {
  background-color: #ff7893c5;
  margin: 0.3rem auto 0;
}

.change-status-button,
.remove-button {
  width: 40%;
  border-radius: 7px;
  font-size: 0.9rem;
  border: none;
  min-height: 1.4rem;
}

.empty-state {
  text-align: center;
  align-items: center;
  gap: 20px;
  color: #555;
  font-style: italic;
}

/* ---------------------header---------------- */
header {
  display: flex;
  justify-content: flex-start;
  width: 70vw;
  align-items: center;
  padding: 30px 0;
  border-bottom: 1px solid #ff7893;
  /* background-color: whitesmoke; */
}

.title {
  flex: 1;
  font-size: 1.2em;
  padding-left: 30px;
}

header img {
  height: 3.5em;
}

header button {
  display: flex;
  justify-content: center;
  flex-direction: column;
  /* align-items:  center; */
  font-size: 28px;
  border: none;
  background-color: white;
}

header button img {
  height: 1em;
  align-self: flex-end;
}

/* -----------------modal------------------ */
.modal {
  position: fixed;
  z-index: 1;
  top: 50%;
  left: 50%;
  width: 600px;
  padding: 30px;
  border-radius: 6px;
  background-color: whitesmoke;
  text-align: center;
  transform: translate(-50%, -50%) scale(0);
  transition: 0.2s ease-in-out;
  font-size: 18px;
  border: 1px solid #59a1a5;
}

.modal img {
  height: 40px;
  align-self: center;
}

.modal.active {
  transform: translate(-50%, -50%) scale(1);
}

.add-book-form {
  display: flex;
  flex-direction: column;
  gap: 30px;
  text-align: left;
}

.book-form-unit {
  display: flex;
  flex-direction: column;
  gap: 5px;
}

.radio-container {
  display: flex;
  justify-content: space-around;
}

.radio-div {
  display: flex;
  flex-direction: column;
  gap: 5px;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  display: none;
  width: 100%;
  height: 100%;
  /* background-color: rgba(0, 0, 0, 0.5); */
  backdrop-filter: blur(5px);
}

.overlay.active {
  display: block;
}

input {
  font-size: 20px;
  border: none;
  outline: none;
  padding: 6px;
  border-radius: 6px;
}

button[type="submit"],
.close-modal-button {
  height: 40px;
  font-size: 22px;
  width: 60%;
  border-radius: 7px;
  font-size: 22px;
  border: none;
  min-height: 1.4rem;
  margin: 0 auto;
  background-color: #59a1a5;
}

.close-modal-button {
  background-color: #ff7893;
}

/* ---------------footer------------- */

footer {
  display: flex;
  padding: 30px;
  border-top: 1px solid #ff7893;
  font-size: 20px;
  justify-content: center;
  /* background-color: whitesmoke; */
}
